.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top:0;
    z-index: 100;
    background-color: white;
}


.header__logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}

.header__input{
    display: flex;
    align-items: center;
    width: 40%;
    border:1px solid lightgray 
}

.header__inputButton{
width: 50px !important;
border-color: #fafafa;
border-left: 1px solid lightgrey;
color:gray;
}

.header__icons{
    display: flex;
    align-items: center;
}

.header__input > input{
    flex:1;
}

.header_icon{
    margin-right: 8px;
}